import React, { Component } from 'react';
import { Row, Col, Form, Select, Input, } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;



@Form.create({ name: 'formScholarshipApproval' })


class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  }

  handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form
        className="ant-advanced-search-form"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <Col span={8} >
            <Form.Item label='Hệ'>
              {getFieldDecorator('system1', {
              })(
                <Select>
                  <Option value="1">Đại học chính quy</Option>
                  <Option value="2">Cao đẳng chính quy</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Khoa'>
              {getFieldDecorator('system2', {
              })(
                <Select>
                  <Option value="1">Cơ-điện tử</Option>
                  <Option value="2">Quản trị kinh doanh</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Khóa'>
              {getFieldDecorator('system3', {
              })(
                <Select>
                  <Option value="1">Khóa 1</Option>
                  <Option value="2">Khóa 2</Option>
                  <Option value="3">Khóa 3</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='CN'>
              {getFieldDecorator('system4', {
              })(
                <Select>
                  <Option value="1">Cơ khí</Option>
                  <Option value="2">Điện tử viễn thông</Option>
                  <Option value="3">Điện công nghiệp</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Lớp'>
              {getFieldDecorator('system5', {
              })(
                <Select>
                  <Option value="1">KT1</Option>
                  <Option value="2">KT2</Option>
                  <Option value="3">KT3</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Mã sinh viên'>
              {getFieldDecorator('system11', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Họ tên'>
              {getFieldDecorator('system12', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar



